import React, { useState } from 'react';
import parse from 'html-react-parser';
import classNames from 'classnames';
import styles from './Member.module.scss';
import сircle from '../../../../assets/images/homepage/jury/circle.svg';

const Member = ({ name, description, photo }) => {
  const [isShowPopup, setStatePopup] = useState(false);

  const changePopupState = () => {
    setStatePopup(!isShowPopup);
    // document.body.style.overflow = 'hidden';
  };

  return (
    <div>
      <div className={styles.wrapper} onClick={changePopupState}>
        <div className={styles.image}>
          <div className={styles.сircle}>
            <img src={сircle} alt="сircle" />
          </div>
          <div className={styles.photo}>
            <img src={photo} alt="photo" />
          </div>
        </div>
        <div className={styles.text}>
          <div className={styles.name}>
            {parse(name)}
          </div>
          <div className={styles.description}>
            {parse(description)}
          </div>
        </div>
      </div>
      <div
        className={classNames({
          [styles.popup]: true,
          [styles.show]: isShowPopup,
        })}
        onClick={changePopupState}
      >
        <div className={styles.content}>
          <div className={styles.image}>
            <div className={styles.сircle}>
              <img src={сircle} alt="сircle" />
            </div>
            <div className={styles.photo}>
              <img src={photo} alt="photo" />
            </div>
          </div>
          <div className={styles.text}>
            <div className={styles.name}>
              {parse(name)}
            </div>
            <div className={styles.description}>
              {parse(description)}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Member;
